import { Config, ExpReportFetcher } from '@abetterchoice/abc_web_core';
import { fetchMpNetwork, wxNetworkRequest } from '../wx_common_network';
import { getDeviceId, postReq } from '../wx_utils';

export class WxExpReporterFetcher extends ExpReportFetcher {
  constructor(config: Config, sdkVersion: string, sdkPlatform: string) {
    super(config, sdkVersion, sdkPlatform);
  }

  protected sendBytesRequestWithBytesResponse(
    url: string,
    reqData: object,
    headers: {},
    timeout: number,
  ): any {
    const reportNetworkRequest: wxNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchMpNetwork(reportNetworkRequest);
  }

  protected sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout?: number,
  ) {
    return postReq(url, reqData, headers, timeout);
  }

  protected getDeviceId(prefix: string) {
    return getDeviceId(prefix);
  }
}
