import { Config, ExpDataReporter } from '@abetterchoice/abc_web_core';
import { WxExpReporterFetcher } from './wx_exp_reporter_fetcher';

export class WxExpReporter extends ExpDataReporter {
  constructor(config: Config, sdkVersion: string, sdkPlatform: string) {
    super(config, sdkVersion, sdkPlatform);
  }
  protected initExpReportFetcher(config: Config): any {
    return new WxExpReporterFetcher(config, this.sdkVersion, this.sdkPlatform);
  }
}
