import { Config, MainSDK } from '@abetterchoice/abc_web_core';
import { WxExpComponent } from './experiment/wx_exp_component';
import { WxFeatureFlagComponent } from './featureflag/wx_feature_flag_component';
import { WxEventComponent } from './event/wx_event_component';

export class ABetterChoiceAPI extends MainSDK {
  constructor(config: Config) {
    super(config);
  }

  initEventImpl(): Promise<boolean> {
    this.eventComponent = new WxEventComponent(this.config);
    return this.eventComponent.initEventData();
  }

  initExperimentImpl(): Promise<boolean> {
    this.expComponent = new WxExpComponent(this.config);
    return this.expComponent.initExpData();
  }

  initFeatureFlagImpl(): Promise<boolean> {
    this.featureFlagComponent = new WxFeatureFlagComponent(this.config);
    return this.featureFlagComponent.initFeatureFlagData();
  }
}
