import { EventDataFetcher } from '@abetterchoice/abc_web_core/lib/event/event_fetcher';
import { postReq } from '../wx_utils';
import { Config } from '@abetterchoice/abc_web_core';

/**
 * @file wx_event_fetcher.ts
 * @author fitcher
 * @date 2024-11-07
 */
export class WxEventFetcher extends EventDataFetcher {
  constructor(config: Config) {
    super(config);
  }

  public sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers?: {} | undefined,
    timeout?: number | undefined,
  ) {
    return postReq(url, reqData, headers, timeout);
  }
}
