import {
  Config,
  ExpComponent,
  EVENT_SDK_VERSION_DEFAULT_VAL,
  EVENT_SDK_PLATFORM_DEFAULT_VAL,
} from '@abetterchoice/abc_web_core';
import { WxExpDataFetcher } from './wx_exp_fetcher';
import { WxExpReporter } from './wx_exp_reporter';
import { WxExpDataStorage } from './wx_exp_storage';

// @ts-ignore
// eslint-disable-next-line no-undef
const SDK_VERSION = TABSDK_VERSION || EVENT_SDK_VERSION_DEFAULT_VAL; // 编译时获取
// @ts-ignore
// eslint-disable-next-line no-undef
const SDK_PLATFORM = TABSDK_PLATFORM || EVENT_SDK_PLATFORM_DEFAULT_VAL;

export class WxExpComponent extends ExpComponent {
  constructor(config: Config) {
    super(config);
  }

  protected initExpFetcher(): any {
    this.expDataFetcher = new WxExpDataFetcher(this.config);
  }

  protected initExpReport(): any {
    this.expDataReporter = new WxExpReporter(this.config, SDK_VERSION, SDK_PLATFORM);
  }

  protected initExpStorage(): any {
    this.expDataStorage = new WxExpDataStorage(this.config);
  }
}
