import { Config, EventReporter } from '@abetterchoice/abc_web_core';
import { fetchMpNetwork, wxNetworkRequest } from '../wx_common_network';
import { postReq } from '../wx_utils';

/**
 * @file wx_event_reporter.ts
 * @author fitcher
 * @date 2024-07-24
 */
export class WxEventReporter extends EventReporter {
  constructor(config: Config) {
    super(config);
  }

  public sendBytesRequestWithBytesResponse(url: string, reqData: object, headers?: {}, timeout?: number) {
    const fetchNetworkRequest: wxNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchMpNetwork(fetchNetworkRequest);
  }

  public sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout?: number,
  ) {
    return postReq(url, reqData, headers, timeout);
  }
}
