import { Config, ExpDataStorage } from '@abetterchoice/abc_web_core';

// @ts-ignore
// eslint-disable-next-line no-undef
const env = wx || qq;

export class WxExpDataStorage extends ExpDataStorage {
  constructor(config: Config) {
    super(config);
  }
  protected getStorageAllKey(): Promise<any | null> {
    return new Promise(((resolve) => {
      env.getStorageInfo({
        success(res) {
          resolve(res.keys);
        },
        fail() {
          resolve(null);
        },
      });
    }));
  }

  protected getStorageString(key: string): Promise<string | null> {
    return new Promise((resolve) => {
      env.getStorage({
        key,
        success(res) {
          resolve(res.data);
        },
        fail() {
          resolve(null);
        },
      });
    });
  }

  protected putStorageString(key: string, data: string): Promise<void> {
    return new Promise((resolve) => {
      env.setStorage({
        key,
        data,
      });
      resolve();
    });
  }

  protected removeStorageString(key: string): Promise<void | null> {
    return new Promise((resolve) => {
      env.removeStorage({
        key,
        success(res) {
          resolve();
        },
        fail() {
          resolve(null);
        },
      });
    });
  }
}
