import { UUID_STROAGE_KEY } from '@abetterchoice/abc_web_core';

// @ts-ignore
const env = wx || qq;

export function isArrayBuffer(obj) {
  return Object.prototype.toString.call(obj) === '[object ArrayBuffer]';
}

export function postReq(
  url: string,
  data: Record<string, unknown>,
  headers: Record<string, string> = {},
  httpTimeout?: number,
): Promise<string> {
  return new Promise((resolve, reject) => {
    const reqObj: any = {
      url,
      data,
      header: { 'Content-Type': 'application/json;charset=UTF-8', ...headers },
      method: 'POST',
      success(res) {
        if (res.statusCode === 200) {
          resolve(res.data);
        } else {
          reject(new Error(`error http code ${res.statusCode}`));
        }
      },
      fail() {
        reject(new Error('error load net data'));
      },
    };
    if (httpTimeout && httpTimeout > 0) {
      reqObj.timeout = httpTimeout;
    }
    env.request(reqObj);
  });
}

export function getSystemInfo(): Map<string, string> {
  const ret = new Map<string, string>();
  const res = env.getSystemInfoSync();
  ret.set('resolution', `${res.screenWidth}*${res.screenHeight}`);
  ret.set('brand', res.brand);
  ret.set('model', res.model);
  ret.set('version', res.version);
  ret.set('SDKVersion', res.SDKVersion);
  ret.set('system', res.system);
  return ret;
}

export function getDeviceId(prefix: string = '') {
  return env.getStorageSync(prefix + UUID_STROAGE_KEY) || '';
}


