/* eslint-disable class-methods-use-this */
import { Config, ExpDataFetcher } from '@abetterchoice/abc_web_core';
import { fetchMpNetwork, wxNetworkRequest } from '../wx_common_network';
import { postReq, getDeviceId } from '../wx_utils';

export class WxExpDataFetcher extends ExpDataFetcher {
  constructor(config: Config) {
    super(config);
  }
  // 通过json请求
  public sendStringRequestWithStringResponse(
    url: string,
    reqData: Record<string, unknown>,
    headers: Record<string, string> = {},
    timeout: number,
  ): Promise<string> {
    return postReq(url, reqData, headers, timeout);
  }

  // 通过PB二进制流请求
  public sendBytesRequestsWithBytesResponse(
    url: string,
    reqData: object,
    headers?: {},
    timeout?: number,
  ) {
    const fetchNetworkRequest: wxNetworkRequest = {
      url,
      data: reqData,
      headers,
      timeout,
    };
    return fetchMpNetwork(fetchNetworkRequest);
  }

  protected getDeviceId(prefix: string) {
    return getDeviceId(prefix);
  }
}
