import { Config, FeatureFlagComponent } from '@abetterchoice/abc_web_core';
import { WxFeatureFlagDataFetcher } from './wx_feature_flag_fetcher';
import { WxFeatureFlagReporter } from './wx_feature_flag_reporter';
import { WxFeatureFlagStorage } from './wx_feature_flag_storage';

/**
 * @file wx_feature_flag_component.ts
 * @author fitcher
 * @date 2024-07-18
 */
export class WxFeatureFlagComponent extends FeatureFlagComponent {
  // private readonly sdkVersion: string;

  constructor(config: Config,  sdkVersion = '') {
    super(config);
    // this.sdkVersion = sdkVersion;
  }

  protected initFeatureFlagFetcher() {
    this.featureFlagFetcher = new WxFeatureFlagDataFetcher(this.config);
  }

  protected initFeatureFlagReporter() {
    this.featureFlagReporter = new WxFeatureFlagReporter(this.config.gameId, this.config.unitId || '');
  }

  protected initFeatureFlagStorage() {
    this.featureFlagStorage = new WxFeatureFlagStorage(this.config);
  }
}
